.lang-select {
  display: none;
  width: 200px;
  margin: var(--gap);
  appearance: none;
  background: var(--panel-bg);
  color: var(--text);
  padding: 0.5rem 1rem;
  border: 1px solid var(--muted);
  border-radius: var(--radius);
  font-size: 1rem;
  cursor: pointer;
  background-image: url("data:image/svg+xml,%3Csvg%20width%3D%2210%22%20height%3D%227%22%20viewBox%3D%220%200%2010%207%22%20xmlns%3D%22http%3A//www.w3.org/2000/svg%22%3E%3Cpath%20d%3D%22M1%201l4%204%204-4%22%20stroke%3D%22%237f00ff%22%20stroke-width%3D%222%22%20fill%3D%22none%22/%3E%3C/svg%3E");
  background-repeat: no-repeat;
  background-position: right 1rem center;
  transition: border-color 0.2s ease, box-shadow 0.2s ease;
}
.lang-select:hover {
  border-color: var(--color-primary);
  box-shadow: 0 0 0 2px rgba(127, 0, 255, 0.2);
}

