/* header.css — премиальный хедер с «вишенками» */

/* Анимация градиента фона */
@keyframes gradientBG {
  0% { background-position: 0% 50%; }
  50% { background-position: 100% 50%; }
  100% { background-position: 0% 50%; }
}

.dark-header {
  position: sticky;
  top: 0;
  width: 100%;
  background: linear-gradient(45deg, #2ecc71, #27ae60, #1abc9c);
  background-size: 300% 300%;
  animation: gradientBG 15s ease infinite;
  backdrop-filter: blur(10px);
  box-shadow: 0 4px 16px rgba(0, 0, 0, 0.25);
  border-radius: 0 0 12px 12px;
  z-index: 1000;
  transition: box-shadow 0.3s ease;
}
.dark-header.scrolled {
  box-shadow: 0 6px 24px rgba(0, 0, 0, 0.35);
}

/* Декоративная полоска внизу хедера */
.dark-header::after {
  content: '';
  position: absolute;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%);
  width: 80%;
  height: 3px;
  background: rgba(255, 255, 255, 0.4);
  border-radius: 2px;
}

.dark-header .header-inner {
  max-width: 1280px;
  margin: 0 auto;
  padding: 1rem 2rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

/* Логотип с пульсацией */
@keyframes pulseLogo {
  0%, 100% { transform: scale(1); }
  50% { transform: scale(1.05); }
}
.dark-header .header-logo {
  font-family: 'Montserrat', sans-serif;
  font-size: 1.8rem;
  font-weight: 800;
  color: #ffffff;
  text-decoration: none;
  letter-spacing: -1px;
  transition: transform 0.3s ease;
}
.dark-header .header-logo:hover {
  animation: pulseLogo 1.5s ease infinite;
}

/* Навигация */
.dark-header .header-nav {
  display: flex;
  align-items: center;
  gap: 2rem;
  position: relative;
}
.dark-header .nav-link {
  position: relative;
  font-family: 'Montserrat', sans-serif;
  font-weight: 500;
  font-size: 1rem;
  color: #ecf0f1;
  text-decoration: none;
  padding: 0.5rem 0;
  transition: color 0.3s ease;
}
.dark-header .nav-link::after {
  content: '';
  position: absolute;
  left: 50%;
  bottom: -4px;
  width: 0;
  height: 3px;
  background: #ffffff;
  border-radius: 2px;
  transition: width 0.4s cubic-bezier(.4,0,.2,1), left 0.4s cubic-bezier(.4,0,.2,1);
}
.dark-header .nav-link:hover {
  color: #ffffff;
}
.dark-header .nav-link:hover::after {
  width: 60%;
  left: 20%;
}
.dark-header .nav-link.active {
  color: #ffffff;
}
.dark-header .nav-link.active::after {
  width: 60%;
  left: 20%;
}

/* Подсветка ссылки при клике */
.dark-header .nav-link:focus::after {
  width: 60%;
  left: 20%;
  background: #1abc9c;
  transition: width 0.2s ease;
}

/* Кнопка «Поддержать» */
.dark-header .nav-button {
  font-family: 'Montserrat', sans-serif;
  font-size: 0.95rem;
  font-weight: 600;
  color: #ffffff;
  background: #16a085;
  padding: 0.6rem 1.5rem;
  border: none;
  border-radius: 30px;
  box-shadow: 0 4px 12px rgba(22,160,133,0.4);
  transition: background 0.3s ease, transform 0.2s ease;
  text-decoration: none;
}
.dark-header .nav-button:hover {
  background: #1abc9c;
  transform: translateY(-2px) scale(1.03);
}
.dark-header .nav-button:active {
  transform: scale(0.95);
}

/* Мобильное меню */
.dark-header .menu-toggle {
  display: none;
}
@media (max-width: 768px) {
  .dark-header .menu-toggle {
    display: block;
    cursor: pointer;
    width: 32px;
    height: 32px;
    position: relative;
    z-index: 1001;
  }
  .dark-header .menu-toggle span,
  .dark-header .menu-toggle span::before,
  .dark-header .menu-toggle span::after {
    content: '';
    position: absolute;
    left: 0;
    width: 100%;
    height: 4px;
    background: #ffffff;
    border-radius: 2px;
    transition: transform 0.3s ease;
  }
  .dark-header .menu-toggle span { top: 50%; transform: translateY(-50%); }
  .dark-header .menu-toggle span::before { top: -10px; }
  .dark-header .menu-toggle span::after { top: 10px; }
  .dark-header.is-open .menu-toggle span { background: transparent; }
  .dark-header.is-open .menu-toggle span::before { transform: translateY(10px) rotate(45deg); }
  .dark-header.is-open .menu-toggle span::after { transform: translateY(-10px) rotate(-45deg); }
  .dark-header .header-nav.desktop { display: none; }
  .dark-header .header-nav.mobile {
    display: flex;
    flex-direction: column;
    gap: 1rem;
    position: fixed;
    top: 0; right: -100%;
    width: 75%; height: 100vh;
    background: rgba(22,160,133,0.95);
    padding: 2rem 1.5rem;
    transition: right 0.3s ease;
  }
  .dark-header.is-open .header-nav.mobile { right: 0; }
}

/* Скрываем пустые выпадающие списки в Settings */
.section.settingsDiv select:empty {
  display: none !important;
}