.greenButton {
  position: relative;
  display: inline-block;
  padding: 0.8rem 2rem;
  margin-top: 1rem;
  margin-left: auto;
  margin-right: auto;
  font-family: 'Montserrat', sans-serif;
  font-weight: 600;
  font-size: 1rem;
  text-transform: uppercase;
  letter-spacing: 1.2px;
  color: #ffffff;
  background: linear-gradient(145deg, #2ecc71 0%, #27ae60 100%);
  border: none;
  border-radius: 40px;
  box-shadow:
    inset 0 -2px 0 rgba(0,0,0,0.2),
    0 8px 15px rgba(0, 0, 0, 0.1);
  transition: transform 0.25s cubic-bezier(.4,0,.2,1),
              box-shadow 0.25s ease,
              background 0.4s ease;
  cursor: pointer;
  overflow: hidden;
}

.greenButton::before {
  content: '';
  position: absolute;
  top: 50%; left: 50%;
  width: 120%; height: 120%;
  background: radial-gradient(circle at center, rgba(46,204,113,0.4), transparent 60%);
  transform: translate(-50%, -50%) scale(0);
  border-radius: 50%;
  transition: transform 0.5s ease-out;
  pointer-events: none;
}

.greenButton:hover {
  transform: scale(1.05);
  box-shadow:
    inset 0 -2px 0 rgba(0,0,0,0.2),
    0 12px 20px rgba(0,0,0,0.15);
}
.greenButton:hover::before {
  transform: translate(-50%, -50%) scale(1);
}

.greenButton:focus {
  outline: none;
  box-shadow:
    0 0 0 4px rgba(46, 204, 113, 0.6),
    inset 0 -2px 0 rgba(0,0,0,0.2);
}

.greenButton:active {
  transform: scale(0.96);
  box-shadow:
    inset 0 2px 4px rgba(0,0,0,0.2),
    0 4px 10px rgba(0,0,0,0.15);
}
.greenButton::after {
  content: '';
  position: absolute;
  border-radius: 50%;
  transform: scale(0);
  background: rgba(255,255,255,0.5);
  opacity: 0;
  pointer-events: none;
}
.greenButton:active::after {
  width: 200px;
  height: 200px;
  top: 50%; left: 50%;
  transform: translate(-50%, -50%) scale(1);
  opacity: 1;
  transition: transform 0.5s ease-out, opacity 0.5s ease-out;
}

.greenButton--small {
  padding: 0.5rem 1rem;
  font-size: 0.85rem;
}
.greenButton--large {
  padding: 1.2rem 2.5rem;
  font-size: 1.2rem;
}

.greenButton:disabled,
.greenButton[disabled] {
  background: #95a5a6;
  box-shadow: none;
  color: #ecf0f1;
  cursor: not-allowed;
  opacity: 0.7;
  transform: none;
}