:root {
  --primary: #00c800;
  --muted: #666;
  --panel-bg: #fff;
  --shadow: rgba(0,0,0,0.08);
  --radius: 12px;
  --font: 'Inter', sans-serif;
}

body {
  font-family: var(--font);
}

.m-header {
  position: sticky;
  top: 0;
  z-index: 100;
  background: var(--panel-bg);
  box-shadow: 0 2px 6px var(--shadow);
}
.m-header__inner {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0.75rem 1rem;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.m-header__logo {
  font-size: 1.5rem;
  font-weight: 700;
  color: #222;
}

.m-header__langs {
  display: flex;
  gap: 0.25rem;
}
.lang-btn {
  background: transparent;
  border: 1px solid var(--muted);
  padding: 0.25rem;
  border-radius: var(--radius);
  font-size: 1rem;
  transition: all 0.2s;
}
.lang-btn.active,
.lang-btn:hover {
  background: var(--primary);
  border-color: var(--primary);
  color: #fff;
}

.m-header__nav {
  display: none;
}
@media (min-width: 768px) {
  .m-header__nav {
    display: flex;
    gap: 0.5rem;
    font-size: 0.9rem;
    color: var(--muted);
    align-items: center;
  }
  .m-header__nav a {
    color: var(--muted);
    text-decoration: none;
  }
  .m-header__nav a:hover {
    color: var(--primary);
    text-decoration: underline;
  }
  .m-header__sep {
    color: var(--muted);
    margin: 0 0.25rem;
    user-select: none;
  }
}
